import React from "react"
import "../styles/_contactMe.scss"
import { Helmet } from "react-helmet"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { config } from "@fortawesome/fontawesome-svg-core"
import {
  faAddressCard,
  faAt,
  faCaretRight,
  faCommentDots,
  faEnvelope,
  faMapMarked,
  faMobileAlt,
  faSignature,
} from "@fortawesome/free-solid-svg-icons"
config.autoAddCss = false

const ContactMe = () => (
  <div className="caja2">
    <div className="addressInfo">
      <h2>
        <span>
          <FontAwesomeIcon icon={faAddressCard} />
        </span>
        Address Information
      </h2>
      <h3>
        <span>
          <FontAwesomeIcon icon={faMapMarked} />
        </span>
        Abra Pampa, Jujuy, Argentina
      </h3>
      <h3>
        <span>
          <FontAwesomeIcon icon={faMobileAlt} />
        </span>
        +543886864008
      </h3>
      <h3>
        <span>
          <FontAwesomeIcon icon={faAt} />
        </span>
        emavilte18@gmail.com
      </h3>
      <iframe
        title="mapa"
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14720.769805152368!2d-65.70457411994427!3d-22.721087161703448!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x94047040297c0cf1%3A0xec2fdbe27a338217!2sAbra%20Pampa%2C%20Jujuy!5e0!3m2!1ses!2sar!4v1606631945319!5m2!1ses!2sar"
      />
    </div>
    <div className="formulario">
      <form
        method="post"
        action="http://localhost:3000/contact"
        encType="application/x-www-form-urlencoded"
      >
        <label>
          <h4>
            <span>
              <FontAwesomeIcon icon={faSignature} />
            </span>
            Name
          </h4>
          <input type="text" name="name" id="name" placeholder="Your name" />
        </label>
        <label>
          <h4>
            <span>
              <FontAwesomeIcon icon={faEnvelope} />
            </span>
            Email
          </h4>
          <input
            type="email"
            name="email"
            id="email"
            placeholder="Your email"
          />
        </label>
        <label>
          <h4>
            <span>
              <FontAwesomeIcon icon={faCaretRight} />
            </span>
            Subject
          </h4>
          <input type="text" name="subject" id="subject" placeholder="Title" />
        </label>
        <label>
          <h4>
            <span>
              <FontAwesomeIcon icon={faCommentDots} />
            </span>
            Message
          </h4>
          <textarea
            name="message"
            id="message"
            rows="5"
            placeholder="AquÃ­ escribe tu consulta :)"
          />
        </label>
        <div>
          <Helmet>
            <script
              src={`https://www.google.com/recaptcha/api.js`}
              async
              defer
            ></script>
           </Helmet>
          <button
          className="g-recaptcha" data-sitekey="6LfRPxYaAAAAAGnOXQNcmQYCBA0qwoR9YPuDOzCj"
            type="submit"
          >
            Send
          </button>
          <input className="clear" type="reset" value="Clear" />
        </div>
      </form>
    </div>
  </div>
)

export default ContactMe
